
.scaling-svg-container {
  position: relative;
  height: 0;
  width: 100%;
  padding: 0;
  padding-bottom: 100%;
}
.scaling-svg {
  max-height: 80vh;
}
.itemLabel, .legendLabel, .areaLabel {
  font-family: 'Oswald', sans-serif;
  opacity: 1;
  -webkit-transform-origin: top right;
          transform-origin: top right;
  -moz-transform-origin: 0 0;
}
.itemLabel:hover {
  font-weight: bold;
}
.itemLabel {
  cursor: pointer;
}
.areaFill {
  opacity: 0.5;
}
.legendLabel{
  fill: white;
}
.legendArcBg {
  fill: #F57C00;
}
.areaLabelArc {
  fill: none;
}



